.calendar__section {
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
    margin-bottom: 80px;
    width: calc(100% - 40px);
    padding: 80px 20px 80px 20px;
    background-color: rgba(218, 118, 243, 0.2);
    overflow: hidden;
    position: relative;
}

.calendar__pic_gross {
    width: 300px;
    max-height: 500px;
    min-height: 500px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;

}

.calendar__pic_gross_right {
    background-image: url(../../images/calendar/gross.png);
    top: -50px;
    right: 0;
    transform: rotate(-90deg);
}

.calendar__pic_gross_left {
    background-image: url(../../images/calendar/gross.png);
    top: 60px;
    left: 0;
    transform: rotate(90deg);
}
