.calendar__wrapper {
    padding: 0;
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: auto;
}

.calendar__monthday_card {
    padding: 10px;
    text-align: center;
    border: 1px solid #ddd;
    border: 1px solid #fff;
    height: 150px;
    transition: background-color 0.5s ease;
    font-size: 26px;
}

.calendar__name_of_current_month {
    font-size: 26px;
    font-weight: bold;
}

.calendar__monthday_card-color {
    background-color: rgba(74, 187, 39, 0.3);
    cursor: pointer;
}

.calendar__monthday_card-color:hover {
    background-color: rgba(74, 187, 39, 0.7);
}

.calendar__monthday_card_event_wrapper {
    padding: 0;
}

.calendar__monthday_card_event { 
    list-style-type: none;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 20px;
}


.calendar__week-days-container {
    margin-top: 20px;
    display: grid; 
    grid-template-columns: repeat(7, 1fr);
}

.calendar__month_btn_container {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    margin-bottom: 50px;
    gap: 10px;

    max-width: 700px;
    justify-content: center;
}

.calendar__next_btn_container {
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
}

.calendar__next_btn {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    padding: 10px;
    border: none;
    /*border: 1px solid rgb(74, 187, 39);*/
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Karton", sans-serif;
    color: rgb(3, 28, 61);
    text-transform: uppercase;
    cursor: pointer;
    background-color: rgba(74, 187, 39, 0.7);
}

.calendar__next_btn:hover {
    border: 1px solid rgba(74, 187, 39, 0.9);
}

.calendar__month {
    border-radius: 20px;
    min-height: 30px;
    min-width: 50px;
    padding: 10px;
    border: none;
    border: 1px solid rgb(24, 124, 38);
    /*border: 1px solid rgb(74, 187, 39);*/
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Karton", sans-serif;
    color: rgb(3, 28, 61);
    text-transform: uppercase;
    cursor: pointer;
    background-color: rgba(74, 187, 39, 0.1);
    font-size: 20px;
}

.calendar__month:hover {
    border: 2px solid rgb(74, 187, 39);
}
.calendar__week-day {
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 28px;
}
@media screen and (max-width: 1500px) {
    .calendar__pic_gross {
        width: 250px;
        max-height: 450px;
        min-height: 450px;
    }
}
@media screen and (max-width: 1200px) {
    .calendar__pic_gross {
        width: 200px;
        max-height: 400px;
        min-height: 400px;
    }
}

@media screen and (max-width: 1050px) {
    .calendar__pic_gross_left {
        top: 30px;
    }
    .calendar__monthday_card_event {
        font-size: 18px;
    }
}

@media screen and (max-width: 800px) {
    .calendar__pic_gross {
        width: 100px;
        max-height: 300px;
        min-height: 300px;
    }
    .calendar__monthday_card_event {
        font-size: 14px;
    }
}

@media screen and (max-width: 750px) {
    .calendar__monthday_card  { 
        font-size: 14px;
        height: 100px;
    }

    .calendar__section {
        margin-top: 50px;
        margin-bottom: 50px;
        padding-top: 50px;
        padding-bottom: 50px;
    }
} 

@media screen and (max-width: 700px) {
    .calendar__monthday_card_event  { 
        font-size: 12px;
    }

    .calendar__next_btn {
        width: 30px;
        height: 30px;
    }    
} 

@media screen and (max-width: 600px) {
    .calendar__monthday_card {
        height: 60px;
        font-size: 10px;
        padding: 5px;
    }

    .calendar__monthday_card_event { 
        font-size: 6px;
    }

    .calendar__week-day {
        font-size: 20px;
    }
} 
@media screen and (max-width: 550px) {
    .calendar__month {
        font-size: 18px;
    }
    .calendar__name_of_current_month {
        font-size: 22px;
    }
    .calendar__pic_gross {
        width: 80px;
        height: 100px;
    }
    .calendar__pic_gross_right {
        top: -90px;
    }
}

@media screen and (max-width: 480px) {
    .calendar__section {
        padding: 50px 10px 50px 10px;
        width: calc(100% - 20px);
    }
}

@media screen and (max-width: 450px) {
    .calendar__monthday_card {
        height: 50px;
        font-size: 8px;
        padding: 3px;
    }
}

@media screen and (max-width: 350px) {
    .calendar__pic_gross {
        width: 70px;
        height: 90px;
    }
    .calendar__pic_gross_right {
        top: -130px;
        right: -40px;
    }
    
    .calendar__pic_gross_left {
        left: -40px;
    }
}