.schedule__section {
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
    margin-bottom: 80px;
    width: calc(100% - 40px);
    padding: 0 20px 0 20px;
}

.schedule__schedule_download_btn {
    width: 300px;
    height: 50px;
    text-decoration: none;
    border-radius: 30px;
    border: 1px solid rgb(139, 206, 139);
    background-color: rgb(129, 187, 129);

    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-top: 100px;

    font-family: "Karton", sans-serif;
    font-size: 22px;
    color: rgb(3, 28, 61);

    position: relative;
}

.schedule__schedule_download_btn_ladybag {
    background-image: url(../../images/schedule/ladybag.png);
    width: 50px;
    max-height: 50px;
    min-height: 40px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    position: absolute;
    top: 5px;
    left: 5px;

    transform: rotate(20deg);
}

.schedule__schedule_download_btn:hover {
    background-color: rgb(118, 209, 118);
}

.schedule__wrapper {
    padding: 0;
    max-width: 1000px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: auto;
    gap: 10px;
    margin: auto;
}
.schedule__card {
    border-radius: 10px;
    list-style-type: none;
    /*padding: 5px 5px 0 5px;*/
    font-family: "Karton", sans-serif;
    font-size: 22px;
    color: rgb(3, 28, 61);
    min-width: 150px;
}

.schedule__card-class-name-container {
    height: fit-content;
    min-height: 50px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.schedule__card-class-name-container_blue {
    background-color: rgba(40, 111, 204, 0.3);
}
.schedule__card-class-name-container_red{
    background-color: rgba(243, 28, 28, 0.3);
}
.schedule__card-class-name-container_green{
    background-color: rgba(74, 187, 39, 0.3);
}
.schedule__card-class-name-container_pink{
    background-color: rgba(221, 32, 221, 0.3);
}
.schedule__card-class-name-container_orange{
    background-color: rgba(233, 106, 21, 0.3);
}
.schedule__card-class-name-container_purple{
    background-color: rgba(218, 118, 243, 0.3);
}

.schedule__card_blue {
    /*border: 1px solid rgb(40, 111, 204);*/
    background-color: rgba(40, 111, 204, 0.1);
}

.schedule__card_red {
    /*border: 1px solid rgb(243, 28, 28);*/
    background-color: rgba(243, 28, 28, 0.1); /* Легкий оттенок красного */
}

.schedule__card_green {
    /*border: 1px solid rgb(74, 187, 39);*/
    background-color: rgba(74, 187, 39, 0.1); /* Легкий оттенок зеленого */
}

.schedule__card_pink {
    /*border: 1px solid rgb(221, 32, 221);*/
    background-color: rgba(221, 32, 221, 0.1); /* Легкий оттенок розового */
}

.schedule__card_orange {
    /*border: 1px solid rgb(233, 106, 21);*/
    background-color: rgba(233, 106, 21, 0.1); /* Легкий оттенок оранжевого */
}

.schedule__card_purple {
    /*border: 1px solid rgb(218, 118, 243);*/
    background-color: rgba(218, 118, 243, 0.1); /* Легкий оттенок фиолетового */
}

.schedule__opacity {
    opacity: 0.6;
}

.schedule__card-class-name {
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 0;
    font-size: 28px;
    margin-top: 0;
}

.schedule__card-class-groups {
    margin-top: 5px;
    text-transform: uppercase;
    font-weight: 700;
}
.schedule__card-class {
    margin-top: 10px;
    margin-bottom: 10px;
}
.schedule__card-class-days {
    margin-top: 10px;
    margin-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
}
.schedule__card-class-times {
    margin-top: 10px;
    margin-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
}
.schedule__card-class-age {
    margin-top: 10px;
    margin-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
}

.schedule__sort-container {
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.schedule__sort-btn-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
}

.schedule__age-btn {
    border-radius: 20px;
    height: 35px;
    cursor: pointer;
    font-family: "Karton", sans-serif;
    color: rgb(3, 28, 61);
}    

.schedule__age-btn_purple {
    border: 1px solid rgb(218, 118, 243);
    background-color: rgba(218, 118, 243, 0.1);
}
.schedule__age-btn_purple:hover {
    border: 3px solid rgb(218, 118, 243);
}

.schedule__age-btn_blue {
    border: 1px solid rgb(40, 111, 204);
    background-color: rgba(40, 111, 204, 0.1);
}
.schedule__age-btn_blue:hover {
    border: 3px solid rgb(40, 111, 204);
}

.schedule__age-btn_orange {
    border: 1px solid rgb(233, 106, 21);
    background-color: rgba(233, 106, 21, 0.1);
}
.schedule__age-btn_orange:hover {
    border: 3px solid rgb(233, 106, 21);
}

.schedule__age-btn_green {
    border: 1px solid rgb(74, 187, 39);
    background-color: rgba(74, 187, 39, 0.1);
}
.schedule__age-btn_green:hover {
    border: 3px solid rgb(74, 187, 39);
}

.schedule__age-btn_yellow {
    border: 1px solid rgb(250, 246, 3);
    background-color: rgba(246, 248, 109, 0.1);
}
.schedule__age-btn_yellow:hover {
    border: 3px solid rgb(250, 246, 3);
}

@media screen and (max-width: 830px) {
    .schedule__wrapper {
        max-width: 100%;
        grid-template-columns: repeat(3, 1fr);
    }
} 

@media screen and (max-width: 750px) {
    .schedule__sort-container {
        flex-direction: column;
    }

    .schedule__sort-btn-container {
        grid-template-columns: repeat(3, 1fr);
        gap: 10px
    }

    .schedule__sort-title {
        margin-bottom: 0;
    }

    .schedule__section {
        margin-top: 50px;
        margin-bottom: 50px;
    }
} 

@media screen and (max-width: 630px) {
    .schedule__wrapper {
        grid-template-columns: repeat(2, 1fr);
    }
    .schedule__card {
        min-width: 130px;
    }
} 

@media screen and (max-width: 550px) {
    .schedule__card-class-name  {
        font-size: 22px;
    }
    .schedule__card {
        font-size: 18px;
    }
}

@media screen and (max-width: 480px) {
    .schedule__section {
        padding: 0 10px 0 10px;
        width: calc(100% - 20px);
    }

    .schedule__schedule_download_btn {
        width: 250px;
        height: 50px;
        margin-top: 50px;
        font-size: 18px;
    }
}

@media screen and (max-width: 400px) {
    .schedule__wrapper {
        gap: 5px;
    }
    .schedule__card {
        min-width: 120px;
    }

    .schedule__sort-btn-container {
        gap: 5px;
    }
}