.schedule-popup {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin: auto;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility 1s, opacity 1s;
    box-sizing: border-box;
    overflow:auto;
    z-index: 10;
}
  
.schedule-popup__opened {
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(4px);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    visibility: visible;
    opacity: 1;
    transition: visibility 1s, opacity 1s;
    overflow: auto;
    z-index: 99;
}

.schedule-popup__picture-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}
  
.schedule-popup__picture-container {
    width: 500px;
    height: fit-content;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    overflow: auto;
    transition: visibility 1s, opacity 1s;
    
    margin-top: 50px;
    border-radius: 10px;
}
.schedule_popup__title {
    font-size: 28px;
  }
.schedule_popup__time {
    font-size: 28px;
    margin: 10px;
}
.schedule_popup__price {
    font-size: 28px;
    margin: 10px;
}
.schedule-popup__close-button {
    background-image: url(../../images/closebtn.png);
    width: 32px;
    height: 32px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
    cursor: pointer;
    align-self: flex-end;
    transition: visibility 1s, opacity 1s;
    align-self: flex-end;
    margin-bottom: 20px;
}

.schedule_popup__img {
    width: 100%;
    min-height: 200px;
    object-fit: contain;
}
    
.schedule-popup__close-button:hover {
    opacity: 60%;
    transition: 0.5s ease;
}
    
@media screen and (max-width: 600px) {
    .schedule-popup__close-button {
        width: 22px;
        height: 22px;
        margin-bottom: 16px;
    }
    .schedule-popup__picture-container {
        width: 300px;
        height: 300px;
      }
}